import moment from "moment";
import React, { useContext, useEffect, useState } from "react";
import {
  Button,
  ButtonGroup,
  ProgressBar,
  ToggleButton,
} from "react-bootstrap";
import { useParams, useNavigate } from "react-router-dom";
import Select from "react-select";
import {
  AccountsContext,
  BotsContext,
  DealsContext,
  ErrorsContext,
} from "../../../App";
import { IBot } from "../../../interfaces/Bot";
import { IDeal } from "../../../interfaces/Deal";
import { fetchBot } from "../../../state/bots/BotsEffects";
import { fetchAllDealsForBot } from "../../../state/deals/DealsEffects";
import {
  is30daysDealsLoaded,
  isAllDealsLoaded,
  selectAllDeals,
  selectAllDealsWithin30Days,
} from "../../../state/deals/DealsSelectors";
import { getDealsLimiter, setDealsLimiter } from "../../../utils/ApiUtils";
import { usePrevious } from "../../../utils/GeneralUtils";
import "./DCABotDetails.css";
import { DCABotDetailsCharts } from "./DCABotDetailsCharts/DCABotDetailsCharts";
import { DCABotDetailsDealsTable } from "./DCABotDetailsDealsTable/DCABotDetailsDealsTable";
import { DCABotDetailsInfo } from "./DCABotDetailsInfo/DCABotDetailsInfo";
import DatePicker from "react-datepicker";

import "react-datepicker/dist/react-datepicker.css";

const statusFilterOptions: any = [
  { value: "all", label: "All" },
  { value: "Cancelled", label: "Cancelled" },
  { value: "StopLoss", label: "StopLoss" },
  { value: "Failed", label: "Failed" },
  { value: "Completed", label: "Completed" },
  { value: "Closed at Market Price", label: "Closed at Market Price" },
  { value: "Liquidated", label: "Liquidated" },
  { value: "Switched", label: "Switched" },
];

const filterByStatus = (deals: IDeal[], status: string) => {
  return status === "all"
    ? deals
    : deals.filter((deal) => deal.localized_status === status);
};

const filterByPair = (deals: IDeal[], pair: string) => {
  return pair === "all" ? deals : deals.filter((deal) => deal.pair === pair);
};

const filterFrom = (deals: IDeal[], dateFrom: any) => {
  return !dateFrom ||
    moment("01-04-2018", "MM-DD-YYYY").isAfter(dateFrom, "day")
    ? deals
    : deals.filter(
        (deal) =>
          moment(deal.closed_at).isSame(dateFrom, "day") ||
          moment(deal.closed_at).isAfter(dateFrom)
      );
};

const filterTo = (deals: IDeal[], dateTo: any) => {
  return !dateTo || moment().isSame(dateTo, "day")
    ? deals
    : deals.filter(
        (deal) =>
          moment(deal.closed_at).isSame(dateTo, "day") ||
          moment(deal.closed_at).isBefore(dateTo)
      );
};

export const DCABotDetails = () => {
  let params = useParams();
  let navigate = useNavigate();

  const { botsState, botsDispatch } = useContext(BotsContext);
  const { dealsState, dealsDispatch } = useContext(DealsContext);
  const {
    botsState: { bots },
  } = useContext(BotsContext);
  const { errorsDispatch } = useContext(ErrorsContext);
  const {
    accountsState: { accounts },
  } = useContext(AccountsContext);

  const [timeRangeValue, setTimeRangeValue] = useState("30d");
  const [botId, setBotId] = useState(NaN);
  const previousBotId = usePrevious(botId);
  const [bot, setBot] = useState<IBot | null>(null);
  const [botsDeals, setBotsDeals] = useState<IDeal[]>([]);
  const [isDataLoading, setIsDataLoading] = useState(false);
  const [isAllDataLoading, setIsAllDataLoading] = useState(false);
  const [account, setAccount] = useState({});
  const [infoKey, setInfoKey] = useState("main");
  const [percentLoadedDeals, setPercentLoadedDeals] = useState(0);
  const [statusFilter, setStatusFilter] = useState("all");
  const [pairFilter, setPairFilter] = useState("all");
  const [pairFilterOptions, setPairFilterOptions] = useState([] as any);
  const [fromFilter, setFromFilter] = useState(new Date("2018-04-02"));
  const [toFilter, setToFilter] = useState(new Date());

  const clearFilters = () => {
    setStatusFilter("all");
    setPairFilter("all");
    setFromFilter(new Date("2018-04-02"));
    setToFilter(new Date());
  };

  const timeRangeRadios = [
    { name: "30 days", value: "30d" },
    { name: "All time", value: "allTime" },
  ];

  const getAccountById = (accountId: any) =>
    accounts.find((account: any) => account.id === accountId);

  const refreshData = () => {
    setDealsLimiter(Math.random());
    fetchBot(botsDispatch, botsState, errorsDispatch, botId);
    fetchAllDealsForBot(dealsState, dealsDispatch, errorsDispatch, botId, true);
  };

  useEffect(() => {
    if (params.botId) {
      setBotId(parseInt(params.botId));
    }
  }, [params]);

  useEffect(() => {
    setDealsLimiter(Math.random());
    if (botId && !previousBotId) {
      fetchAllDealsForBot(dealsState, dealsDispatch, errorsDispatch, botId);
    }
  }, [botId]);

  useEffect(() => {
    if (bot && accounts.length > 0) {
      setAccount(getAccountById(bot.account_id));
    }
  }, [bot, accounts]);

  useEffect(() => {
    if (botId) {
      setBot(bots[botId]);
    }
  }, [botId, bots]);

  useEffect(() => {
    if (botId) {
      if (timeRangeValue === "30d") {
        setIsDataLoading(!is30daysDealsLoaded(dealsState, botId));
      } else if (timeRangeValue === "allTime") {
        setIsDataLoading(!isAllDealsLoaded(dealsState, botId));
      }
      setIsAllDataLoading(!isAllDealsLoaded(dealsState, botId));
    }
  }, [dealsState, timeRangeValue, botId]);

  useEffect(() => {
    if (botId) {
      let deals: any = [];
      if (timeRangeValue === "30d") {
        deals = selectAllDealsWithin30Days(dealsState, botId);
      } else if (timeRangeValue === "allTime") {
        deals = selectAllDeals(dealsState, botId);
      }
      clearFilters();
      setBotsDeals(deals);
    }
  }, [isDataLoading, isAllDataLoading, timeRangeValue, botId]);

  useEffect(() => {
    if (botId) {
      let deals: any = [];
      if (timeRangeValue === "30d") {
        deals = selectAllDealsWithin30Days(dealsState, botId);
      } else if (timeRangeValue === "allTime") {
        deals = selectAllDeals(dealsState, botId);
      }
      deals = filterByPair(deals, pairFilter);
      deals = filterByStatus(deals, statusFilter);
      deals = filterTo(deals, toFilter);
      deals = filterFrom(deals, fromFilter);
      setBotsDeals(deals);
    }
  }, [pairFilter, statusFilter, fromFilter, toFilter]);

  useEffect(() => {
    if (
      botId &&
      bot &&
      dealsState.deals[botId] &&
      dealsState.deals[botId].offset
    ) {
      const loadedDeals = dealsState.deals[botId].offset || 0;
      setPercentLoadedDeals(
        Math.ceil((loadedDeals * 100) / parseFloat(bot.finished_deals_count))
      );
    }
  }, [dealsState, botId, bot]);

  useEffect(() => {
    if (bot) {
      setPairFilterOptions([
        { value: "all", label: "All" },
        ...bot.pairs.map((pair) => ({
          value: pair,
          label: pair.split("_").reverse().join("/"),
        })),
      ]);
    }
  }, [bot]);

  useEffect(() => {
    return () => {
      // reset limiter everytime to stop all the jobs
      const dealsLimiter = getDealsLimiter();
      dealsLimiter
        .stop({ dropWaitingJobs: true })
        .then(() => dealsLimiter.disconnect());
    };
  }, []);

  return (
    <div className="DCABotDetails">
      <Button
        variant="primary"
        size="sm"
        className="DCABotDetailsGetBackButton"
        onClick={() => {
          navigate(-1);
        }}
      >
        {`< Back to bots list`}
      </Button>
      {!isAllDataLoading && (
        <Button
          variant="primary"
          size="sm"
          onClick={() => {
            refreshData();
          }}
        >
          {`Refresh data`}
        </Button>
      )}
      {isDataLoading && !percentLoadedDeals && (
        <>
          <div className="BotDetailsProgressBarContainer">
            <ProgressBar now={100} animated className="BotDetailsProgressBar" />
          </div>
        </>
      )}
      {isDataLoading && !!percentLoadedDeals && (
        <>
          <div className="BotDetailsProgressBarContainer">
            <ProgressBar
              now={percentLoadedDeals}
              label={`${percentLoadedDeals}%`}
              className="BotDetailsProgressBar"
            />
          </div>
        </>
      )}
      {!isDataLoading && bot && account && (
        <>
          <DCABotDetailsCharts
            bot={bot}
            deals={botsDeals}
            account={account}
            infoKey={infoKey}
            timeRangeValue={timeRangeValue}
          />
          <div className="BotDetailsControlsContainer">
            <div className="BotDetailsControl">
              <ButtonGroup>
                {timeRangeRadios.map((radio, idx) => (
                  <ToggleButton
                    key={idx}
                    id={`radio-${idx}`}
                    type="radio"
                    variant="outline-primary"
                    name="radio"
                    value={radio.value}
                    checked={timeRangeValue === radio.value}
                    onChange={(e) => setTimeRangeValue(e.currentTarget.value)}
                  >
                    {radio.name}
                  </ToggleButton>
                ))}
              </ButtonGroup>
            </div>
            <div className="BotDetailsControl">
              <div className="BotDetailsFilterTitle">Status:</div>
              <Select
                onChange={(option: any): void => {
                  setStatusFilter(option.value);
                }}
                value={
                  statusFilterOptions.find(
                    (option: any) => option.value === statusFilter
                  ) as any
                }
                className="BotDetailsStatusFilter"
                theme={(theme) => ({
                  ...theme,
                  colors: {
                    ...theme.colors,
                    primary25: "#00a59a",
                    primary: "#00a59a",
                  },
                })}
                options={statusFilterOptions as any}
              />
            </div>
            {bot && bot.pairs.length > 1 && (
              <div className="BotDetailsControl">
                <div className="BotDetailsFilterTitle">Pair:</div>
                <Select
                  onChange={(option: any): void => {
                    setPairFilter(option.value);
                  }}
                  value={
                    pairFilterOptions.find(
                      (option: any) => option.value === pairFilter
                    ) as any
                  }
                  className="BotDetailsPairFilter"
                  options={pairFilterOptions as any}
                  theme={(theme) => ({
                    ...theme,
                    colors: {
                      ...theme.colors,
                      primary25: "#00a59a",
                      primary: "#00a59a",
                    },
                  })}
                />
              </div>
            )}
            {timeRangeValue === "allTime" && (
              <>
                <div className="BotDetailsControl">
                  <div className="BotDetailsFilterTitle">From:</div>
                  <DatePicker
                    dateFormat="dd/MM/yyyy"
                    selected={fromFilter}
                    onChange={(date) => {
                      setFromFilter(date as any);
                    }}
                    selectsStart
                    startDate={fromFilter}
                    endDate={toFilter}
                    className="BotDetailsDateFilter"
                    strictParsing
                  />
                </div>
                <div className="BotDetailsControl">
                  <div className="BotDetailsFilterTitle">To:</div>
                  <DatePicker
                    dateFormat="dd/MM/yyyy"
                    selected={toFilter}
                    onChange={(date) => setToFilter(date as any)}
                    selectsEnd
                    startDate={fromFilter}
                    endDate={toFilter}
                    minDate={fromFilter}
                    className="BotDetailsDateFilter"
                    strictParsing
                  />
                </div>
              </>
            )}
            <div className="BotDetailsControl">
              <Button
                variant="primary"
                onClick={() => {
                  clearFilters();
                }}
              >
                {`Clear filters`}
              </Button>
            </div>
          </div>
          <div className="DCABotInfoGroup">
            <div className="DCABotName">{bot.name}</div>
            <div className="DCABotId">{`(Bot ID: ${bot.id})`}</div>
            <div className="DCABotLinkTo3Commas">
              <Button
                onClick={() => {
                  window.open(`https://3commas.io/bots/${botId}`, "_blank");
                }}
                variant="link"
                size="sm"
                active
              >
                Open in 3Commas
              </Button>
            </div>
          </div>
          <DCABotDetailsInfo
            bot={bot}
            deals={botsDeals}
            account={account}
            infoKey={infoKey}
            setInfoKey={setInfoKey}
            timeRangeValue={timeRangeValue}
          />
          {botsDeals.length > 0 && (
            <DCABotDetailsDealsTable bot={bot} deals={botsDeals} />
          )}
        </>
      )}
    </div>
  );
};
