const Router = () => {
  return (
    <Routes>
      <Route path="/" element={<App />}>
        <Route index element={<DCABotsList />} />
        <Route path=":botId" element={<DCABotDetails />} />
      </Route>
    </Routes>
  );
};

export default Router;

