import { IDeal } from "../../interfaces/Deal";

interface IGetDealsList {
  type: "getDealsList";
  payload: { deals: Array<IDeal>; botId: number; periodInDays: number };
}

export const getDealsList = (
  deals: Array<IDeal>,
  botId: number,
  periodInDays: number
): IGetDealsList => ({
  type: "getDealsList",
  payload: { deals, botId, periodInDays },
});

interface ISetOffset {
  type: "setOffset";
  payload: { botId: number; offset: number };
}

export const setOffset = (botId: number, offset: number): ISetOffset => ({
  type: "setOffset",
  payload: { botId, offset },
});

interface IResetDeals {
  type: "resetDeals";
  payload: { botId: number };
}

export const resetDeals = (botId: number): IResetDeals => ({
  type: "resetDeals",
  payload: { botId },
});

export type DealsAction = IGetDealsList | ISetOffset | IResetDeals;
