import React, { useContext, useEffect, useState } from "react";
import { ErrorsContext, StrategiesContext } from "../../../../App";
import { IBot } from "../../../../interfaces/Bot";
import { IDeal } from "../../../../interfaces/Deal";
import { getAllActiveDeals } from "../../../../state/deals/DealsEffects";
import {
  getCurrencyFormat,
  getFundsLockedInBotCurrency,
  getMaxUsageInBotCurrency,
  getReservedFundsInBotCurrency,
} from "../../../../utils/BotsUtils";
import { getAverageDealDuration } from "../../../../utils/DealsUtils";
import { getBotsStrategyName } from "../../../BotsList/BotPreview/BotPreview";
import "./DCABotDetailsMainInfo.css";

export const DCABotDetailsMainInfo = ({
  account,
  bot,
  deals,
}: {
  account: any;
  bot: IBot;
  deals: IDeal[];
}) => {
  const { strategiesState } = useContext(StrategiesContext);
  const { errorsDispatch } = useContext(ErrorsContext);

  const [fundsLocked, setFundsLocked] = useState({
    value: 0,
    currency: "",
  });

  const [fundsLockedShare, setFundsLockedShare] = useState(0);

  const [maxUsage, setMaxUsage] = useState({
    value: 0,
    currency: "",
  });
  const [reservedFunds, setReservedFunds] = useState({
    value: 0,
    currency: "",
  });

  const percentFormatter = new Intl.NumberFormat("en-US", {
    style: "percent",
    minimumFractionDigits: 2,
  });

  const botPairs =
    bot.pairs.length === 1
      ? bot.pairs[0].split("_").reverse().join("/")
      : `${bot.pairs.length} pairs`;
  const botMarket =
    bot.pairs.length === 1 ? "" : `${bot.pairs[0].split("_")[0]} market`;

  const startCondition = bot.strategy_list.map((strategy: any) => (
    <div key={`${Math.random()}`}>{`${getBotsStrategyName(
      strategiesState,
      strategy.strategy
    )}${
      strategy.options.type
        ? `, ${getBotsStrategyName(strategiesState, strategy.options.type)}`
        : ``
    }${strategy.options.percent ? `, ${strategy.options.percent}%` : ``}${
      strategy.options.time ? `, ${strategy.options.time}` : ``
    }${strategy.options.points ? `, ${strategy.options.points}` : ``}`}</div>
  ));

  useEffect(() => {
    if (bot.id && account.id) {
      getMaxUsageInBotCurrency(bot, account).then((mxUsg) => {
        setMaxUsage(mxUsg);
      });

      getAllActiveDeals(errorsDispatch, bot.id).then((activeDeals: IDeal[]) => {
        setReservedFunds(
          getReservedFundsInBotCurrency(activeDeals, bot, account)
        );
        setFundsLocked(getFundsLockedInBotCurrency(activeDeals, bot, account));
      });
    }
  }, [bot, account]);

  useEffect(() => {
    if (maxUsage.value && fundsLocked.value) {
      setFundsLockedShare(fundsLocked.value / maxUsage.value);
    }
  }, [maxUsage, fundsLocked]);

  const avgDealDuration = getAverageDealDuration(deals);

  return (
    <div className="BotInfoTable">
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Account</div>
        <div className="BotInfoTableRow">{account.name}</div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Bot pairs</div>
        <div className="BotInfoTableRow">
          <div>{botPairs}</div>
          <div>{botMarket}</div>
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Deal start condition</div>
        <div className="BotInfoTableRow">{startCondition}</div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Active deals/Max</div>
        <div className="BotInfoTableRow">{`${bot.active_deals_count}/${bot.max_active_deals}`}</div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Funds used</div>
        <div className="BotInfoTableRow">
          <div>{`${getCurrencyFormat(fundsLocked)} ${
            fundsLocked.currency
          }`}</div>
          <div style={{ fontSize: "10px" }}>{`${percentFormatter.format(
            fundsLockedShare
          )} of max usage`}</div>
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Max usage</div>
        <div className="BotInfoTableRow">{`${getCurrencyFormat(maxUsage)} ${
          maxUsage.currency
        }`}</div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Reserved funds</div>
        <div className="BotInfoTableRow">{`${getCurrencyFormat(
          reservedFunds
        )} ${maxUsage.currency}`}</div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Total finished deals</div>
        <div className="BotInfoTableRow">{deals.length}</div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Avg deal duration</div>
        <div className="BotInfoTableRow">{`${avgDealDuration} hrs`}</div>
      </div>
    </div>
  );
};
